<template>
  <div class="source-data-box">
    <div class="tab-title">工作表列表</div>
    <base-table class="source-table" :list="tableList">
      <template slot="header" slot-scope="i">
        <th class="source-name">工作表名称</th>
        <th class="source-time">更新时间</th>
        <th class="source-controls">操作</th>
      </template>
      <template slot-scope="iData">
        <td>{{iData.item.sheet_name}}</td>
        <td>{{formatTime(iData.item.utime)}}</td>
        <td class="base-center">
          <span class="base-table-btn" @click="addDataDialogDisplay=true">添加</span>
          <router-link class="base-table-btn" to="/SourceData/Preview">预览</router-link>
        </td>
      </template>
    </base-table>
    <base-dialog class="add-data-dialog" title="添加数据" :show="addDataDialogDisplay" @close="addDataDialogDisplay=false">
      <div class="base-btn upload-file-btn">
        <span>上传本地文件</span>
        <input type="file" @change="loading" />
      </div>
      <div class="upload-file-tips">仅支持xls(excel2003)文件格式，<br />单个文件建议控制在50MB以内</div>
    </base-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tableList: [],
      addDataDialogDisplay: false
    }
  },
  beforeMount () {
    this.$services.manage.getSourceWorkTableList().then(res => {
      this.tableList = res.data
    })
  },
  methods: {
    formatTime (number) {
      return new Date(number * 1000).format('yyyy-MM-dd hh:mm:ss')
    },
    loading () {
      this.$loading({
        title: '正在上传',
        msg: '正在上传中，请稍后...'
      })
      setTimeout(() => {
        this.$loading.close()
        this.confirmRecoverTips()
      }, 3000)
    },
    alertSuccess () {
      this.$dialog.alert({
        type: 'success',
        msg: '添加数据成功！'
      })
    },
    alertFail () {
      this.$dialog.alert({
        type: 'error',
        msg: '添加数据失败！'
      })
    },
    confirmRecoverTips () {
      this.$dialog.confirm({
        title: '提示',
        msg: '是否继续上传并覆盖原始数据？',
        onSure: () => {
          this.alertSuccess()
        },
        onCancel: () => {
          this.alertFail()
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.source-data-box {
  .tab-title {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .add-data-dialog {
    .upload-file-btn {
      margin: 0 auto;
      @include btn-size(145px, 40px, $font-h6);
      @include upload-btn;
    }
    .upload-file-tips {
      margin-top: 28px;
      font-size: 12px;
      color: $color-warning;
      line-height: 1.2em;
    }
  }
}
</style>



